#AboutMe {
    padding: 80px 170px;
}

#AboutMe p {
    font-size: 18px;
}

#AboutMe ul li {
    display: inline;
    margin-right: 20px;
}

#AboutMe p > img {
    border-radius: 100%;
}

#AboutMeMobile h1 {
    text-align: center;
}

#AboutMeMobile p {
    font-size: 18px;
}

#AboutMeMobile ul {
    text-align: center;
}

#AboutMeMobile ul li {
    display: inline;
    margin-right: 20px;
    text-align: center;
}

#AboutMeMobile p > img {
    border-radius: 100%;
    width: 150px;
    height: 150px;
}